@-webkit-keyframes fadeInDownVisible {
  from {
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0)
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0)
  }
}

@keyframes fadeInDownVisible {
  from {
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0)
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0)
  }
}

.List-Photo_hover {
  display: none;
}

.List-Item:hover .List-Photo_hover {
  display: block;
  -webkit-animation: fadeInDownVisible .5s ease;
          animation: fadeInDownVisible .5s ease;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}


